/* stylelint-disable-next-line selector-class-pattern -- Third Party Class */
:global(.infinite-scroll-component__outerdiv) {
  width: 100%;
}

.sb-infinite-scroll {
  background-color: var(--sbt-color-background);
  display: flex;
  flex-direction: column;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  position: relative;
  width: 100%;

  @media not all and (min-resolution: 0.001dpcm) {
    will-change: transform;
  }
}
